body {
  width: 500px;
  margin: 0 auto;
  text-align: center;
}

.next-button,
.remove-button,
.add-button {
  float: right;
}

.previous-button {
  float: left;
}

.grid-container {
  width: 500px;
  display: grid;
  grid-row-gap: 1px;
  grid-column-gap: 3px;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(6, auto);
}

.day {
  width: 100%;
  border: 1px solid black;
  text-align: center;
  font-family: 'Anonymous Pro', monospace;
  font-size: small;
}

.favourite-team {
  text-align: left;
  color: white;
}

.new-team {
  text-align: left;
}

.favourite-team,
.new-team {
  margin: 2px 0;
  padding: 2px;
}

.favourite-team > *,
.new-team > * {
  margin: 2px;
}

